:root {
  --text: #222;
}

body {
  margin: 0;
  background-color: #444;
}

h1 {
  height: 100vh;
  display: flex;
  color: var(--text);
  align-items: center;
  justify-content: center;
  font-size: 2em;
  font-family: sans-serif;
  text-shadow: 0 0 20px black;
}

div {
  color: #326432;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  font-size: 1.5rem;
}